import React, { Component } from "react";
import Lista from "./lista";

class Cantidad extends Component {
  render() {
    const {
      onIncrement,
      onDelete,
      onDecrement,
      counters,
    } = this.props;
    return (
      <div>
        {counters.map(counter => (
          <Lista
            key={counter.id}
            counter={counter}
            onIncrement={onIncrement}
            onDecrement={onDecrement}
            onDelete={onDelete}
          />
        ))}
      </div>
    );
  }
}

export default Cantidad;
