import React from 'react';


const Carrito = ({ objsTotales }) => (
    <nav className="badge badge-dark  bg-dark  carrito float-right">
        <div className="navbar-brand">
            <i className="fa fa-shopping-cart fa-lg " aria-hidden="false" />
            <span className={objsTotales > 0 ? 'badge badge-pill badge-success m-3' : 'badge-pill badge badge-danger m-3'} style={{ width: 60 }} >
                {objsTotales}
            </span>
                Artículos
        </div>
    </nav>
);
export default Carrito