import React, {Component} from 'react';
import './App.css';

//Components
import Carrito from './components/Carrito'
import Cantidad from "./components/Cantidad";

class App extends Component {

 
  state = {
    counters: [
      { id: 1, desc: 'Pantalón', value: 0},
      { id: 2, desc: 'Blusa' , value: 0},
      { id: 3, desc: 'Blusa' , value: 0},
      { id: 4, desc: 'Gorra' , value: 0},
      { id: 5, desc: 'Zapatos' , value: 0},
      { id: 6, desc: 'Tenis' , value: 0}
    ]
  };

  handleIncrement = counter => {
    const counters = [...this.state.counters];
    const index = counters.indexOf(counter);
    counters[index] = { ...counters[index] };
    counters[index].value++;
    this.setState({ counters });
    
  };

  handleDecrement = counter => {
    const counters = [...this.state.counters];
    const index = counters.indexOf(counter);
    counters[index] = { ...counters[index] };
    counters[index].value--;
    this.setState({ counters });
  };


  handleDelete = counterId => {
    const counters = this.state.counters.filter(c => c.id !== counterId);
    this.setState({ counters });
  };

  handleRestart = () => {
    window.location.reload();
  };

  render() {
    return (
      <>

      <div style={{marginTop: '40px'}}>
      <h1 style={{marginLeft: "40px", textAlignVertical: "center", color: "darkblue"}}> Carrito de compras </h1>
      </div>
        <main className="container">
          <Cantidad
            counters={this.state.counters}
            onReset={this.handleReset}
            onIncrement={this.handleIncrement}
            onDecrement={this.handleDecrement}
            onDelete={this.handleDelete}
            onRestart={this.handleRestart}
          />
        </main>
        <div className="container">
        <Carrito
          objsTotales={this.state.counters.filter(c => c.value > 0).length}
        />
        </div>
        </>
    );
  }
}

export default App;
