import React, { Component } from "react";

class Lista extends Component {
  render() {
    return (
        <div className="row">
          <div className="col-md-3">
            <span style={{ fontSize: 24 }} className="badge m-2 badge-dark">
              {this.props.counter.desc}
            </span>
          </div>
          <div className="col-md-1">
            <span style={{ fontSize: 24 }} className={this.getBadgeClasses()}>
              {this.formatCount()}
            </span>
          </div>
          <div className="col-md-4">
            <button className="btn btn-success" onClick={() => this.props.onIncrement(this.props.counter)}
            >
              <i className="fa fa-cart-plus" aria-hidden="true" />
            </button>
            <button
              className={this.props.counter.value > 0 ? 'btn btn-warning' : 'btn btn-secondary'} 
              onClick={() => this.props.onDecrement(this.props.counter)} 
              disabled={this.props.counter.value === 0 ? "disabled" : ""}
            >
              <i className="fa fa-cart-arrow-down" aria-hidden="true" />
            </button>
            <button
              className="btn btn-danger"
              onClick={() => this.props.onDelete(this.props.counter.id)}
            >
              <i className="fa fa-trash-o" aria-hidden="true" />
            </button>
          </div>
        </div>
    );
  }

  getBadgeClasses = () => {
    let classes = "badge m-2 badge-";
    classes += this.props.counter.value === 0 ? "secondary" : "primary";
    return classes;
  };

  formatCount = () => {
    const { value } = this.props.counter;
    return value === 0 ? "0" : value;
  };
}

export default Lista;
